import { Component, Input, Output, EventEmitter, ViewChild, TemplateRef, ComponentFactoryResolver, Injector } from '@angular/core';
import { FrameContext } from '@farris/devkit';
import { NotifyService } from '@farris/ui-notify';
import { DataExportService } from '../service/DataIEService/data-export.service';
import { DataExchangeModel } from '../model/Model/DataExchangeModel';
import { DataImportExportRule } from '../model/DataImportExportRule';
import { ExportMethodType } from '../model/DataIEType/ExportMethodType';
import { FileOperateService } from '../service/DataIEService/file-operate.service';
import { EntityListService } from '@farris/command-services';
import { ElementSelectComponent } from '../element-select/element-select.component';
import { CustomDataIEService } from '../service/DataIEService/custom-data-ie.service';
// import { FilterAndSortComponent } from '../filter-and-sort/filter-and-sort.component';
import { BsModalService } from '@farris/ui-modal';
import { DataIEUtils } from '../service/Utils/DataIEUtils';
import { LoadingService, LoadingComponent } from '@farris/ui-loading';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import {MethodType} from '../model/DataIEType/MethodType';

@Component({
    selector: 'app-export-dialog',
    templateUrl: './export-dialog.component.html',
    styleUrls: ['./export-dialog.component.css'],
    providers: [NotifyService]
})
//èªå®ä¹å¯¼åº
export class ExportDialogComponent {
    @ViewChild('selectFooter') modalFooter: TemplateRef<any>;
    // è§åID
    @Input()
    ruleId = '';
    @Input()
    i18nId = '';
    @Input()
    exportOption: any = null;
    @Input()
    isHandCraft: boolean = false;
    subject: Subject<Boolean> = null;
    @Input()
    // repository = null;
    frameContext: FrameContext = null;
    @Input()
    methodType: MethodType;
    @Input()
    show: boolean = false;
    @Output()
    needDestroy = new EventEmitter<boolean>();
    valueField: string = 'Office Excel97-2003(*.xls)';
    exportRule: DataImportExportRule;
    @ViewChild(ElementSelectComponent)
    eleSelectComp: ElementSelectComponent;
    @Input()
    Model: any = null;
    @Input()
    components: any = null;
    @Input()
    filter: any = null;
    @Input()
    InnerModel: any = null;
    @Input()
    CustomModel: any = null;
    @Input()
    isCustomExport: any = null;



    export_success: string;
    export_fail: string;
    file_type: string;
    download_vsto: string;
    ie_cancel: string;
    ie_ok: string;
    customexport_no_column: string;
    vsto_component: string;
    constructor(
        private dataExportService: DataExportService,
        private notify: NotifyService,
        private fileService: FileOperateService,
        private entityService: EntityListService,
        private customIEService: CustomDataIEService,
        private resolver: ComponentFactoryResolver,
        private modalService: BsModalService,
        private http: HttpClient
    ) {
        DataIEUtils.getTranslateValue(http).subscribe(res => {
            this.export_success = res['export_success'];
            this.export_fail = res['export_fail'];
            this.file_type = res['file_type'];
            this.download_vsto = res['download_vsto'];
            this.ie_cancel = res['ie_cancel'];
            this.ie_ok = res['ie_ok'];
            this.customexport_no_column = res['customexport_no_column'];
            this.vsto_component = res['vsto_component'];
        });
    }
    public DownloadVSTO() {
        const repository = this.frameContext === null ? null : this.frameContext.repository;
        if (this.isHandCraft === true) {
            const su = DataIEUtils.ChooseSu(repository, this.exportOption);
            this.fileService.DownloadFile4HandCraft('æ°æ®å¯¼åºç»ä»¶.msi', this.vsto_component, su, false)
        } else {
            const befService = DataIEUtils.GetBefRestService(repository);
            this.fileService.DownloadFile('æ°æ®å¯¼åºç»ä»¶.msi', this.vsto_component, befService.baseUri, false);
        }
    }
    public close() {
        this.needDestroy.emit();
    }
    // public fileListItems = [
    //     {id:'Office Excel97-2003(*.xls)',label:'Office Excel97-2003(*.xls)'},
    //     {id:'Office Excel(*.xlsx)',label:'Office Excel(*.xlsx)'},
    //     {id:'Office Excel Macro(*.xlsm)',label:'Office Excel Macro(*.xlsm)'},
    //     {id:'WPS Table(*.et)',label:'WPS Table(*.et)'}
    //     // 'WPS Table(Template File)(*.ett)'
    // ];
    public fileListItems = [
        'Office Excel97-2003(*.xls)',
        'Office Excel(*.xlsx)',
        'Office Excel Macro(*.xlsm)',
        'WPS Table(*.et)'
        // 'WPS Table(Template File)(*.ett)'
    ];
    public Ok() {
        if (this.ruleId === '' && this.InnerModel === null) {
            return;
        }
        let suffix = '.xls';
        switch (this.valueField) {
            case 'Office Excel97-2003(*.xls)':
                suffix = '.xls';
                break;
            case 'Office Excel(*.xlsx)':
                suffix = '.xlsx';
                break;
            case 'Office Excel Macro(*.xlsm)':
                suffix = '.xlsm';
                break;
            case 'WPS Table(*.et)':
                suffix = '.et';
                break;
            case 'WPS Table(Template File)(*.ett)':
                suffix = '.ett';
                break;
            default:
                break;
        }
        if (this.isCustomExport === false) {
            suffix = null;
        }
        //èªå®ä¹å¯¼åºåæ¯
        if (this.InnerModel !== null) {
            this.eleSelectComp.Save();
            const elements = this.InnerModel.MainObject.Elements;
            const mainObjName = this.InnerModel.MainObject.FileObjectName;
            if (elements == null || elements === undefined || elements.length === 0) {
                this.notify.info(mainObjName + ' ' + this.customexport_no_column);
                return;
            }
            this.customIEService.SaveCustomExportRule(this.InnerModel, this.components, this.frameContext, suffix, this.i18nId,this.exportOption.dimension).subscribe(
                data => {
                    if (data['success'] === true) {
                        this.customIEService.ExportByRule(
                            ['DataExport', this.Model['MainObject']['Source']['SourceMetadataID'],this.exportOption.dimension],
                            this.filter,
                            this.exportOption,
                            this.frameContext);
                    } else {
                        this.notify.error(this.export_fail + data['message']);
                    }
                }
            );
            const unscriber = this.customIEService.export.subscribe(data => {
                if (data === '') {
                    this.notify.success(this.export_success);
                    unscriber.unsubscribe();
                    this.subject.next(true);
                    this.subject.complete();
                } else {
                    const errorlevel = this.customIEService.errorlevel;
                    if (errorlevel === 'Info') {
                        this.notify.info(data);
                    } else if (errorlevel === 'Warning') {
                        this.notify.warning(data);
                    } else {
                        this.notify.error(this.export_fail + data);
                    }
                    // this.notify.error('å¯¼åºå¤±è´¥ï¼');
                    unscriber.unsubscribe();
                    this.subject.next(false);
                    this.subject.complete();
                }
                this.needDestroy.emit();
            });
        }
        //éVSTOç¹æ§ä¸çæ®éå¯¼åºåæ¯
        if (this.ruleId !== '') {
            switch (this.methodType) {
                case MethodType.CommonExport:
                  this.dataExportService.ExportById(this.ruleId, this.frameContext, this.exportOption, suffix);
                  const unscriber = this.dataExportService.export.subscribe(data => {
                        if (data === '') {
                            this.notify.success(this.export_success);
                            unscriber.unsubscribe();
                            this.subject.next(true);
                            this.subject.complete();
                        } else {
                            const errorlevel = this.dataExportService.errorlevel;
                            if (errorlevel === 'Info') {
                                this.notify.info(data);
                            } else if (errorlevel === 'Warning') {
                                this.notify.warning(data);
                            } else {
                                this.notify.error(this.export_fail + data);
                            }
                            // this.notify.error('å¯¼åºå¤±è´¥ï¼');
                            unscriber.unsubscribe();
                            this.subject.next(false);
                            this.subject.complete();
                        }
                        this.needDestroy.emit();
                    });
                    break;
                case MethodType.ExportCurrentTableData:
                  this.dataExportService.ExportCurrentData(this.ruleId, this.frameContext, this.entityService.pageIndex,
                    this.entityService.pageSize, this.exportOption, suffix);
                  const exportUnscribe = this.dataExportService.export.subscribe(data => {
                        if (data === '') {
                            this.notify.success(this.export_success);
                            exportUnscribe.unsubscribe();
                            this.subject.next(true);
                            this.subject.complete();
                        } else {
                            const errorlevel = this.dataExportService.errorlevel;
                            if (errorlevel === 'Info') {
                                this.notify.info(data);
                            } else if (errorlevel === 'Warning') {
                                this.notify.warning(data);
                            } else {
                                this.notify.error(this.export_fail + data);
                            }
                            exportUnscribe.unsubscribe();
                            this.subject.next(false);
                            this.subject.complete();
                        }
                        this.needDestroy.emit();
                    });
                    break;
            }
            // this.needDestroy.emit();
        }
    }
    public Cancel() {
        this.needDestroy.emit(true);
    }
}
