import {Repository} from '@farris/devkit';
import {BefRepository, BefRestService} from '@farris/bef';
import {DefaultLang} from '../../i18n/default-lang';
import {HttpClient} from '@angular/common/http';
import {Observable, of, Subject} from 'rxjs';
import {FileConst} from '../../model/utils/FileConst';

export function isNullOrUndefined(obj: any): boolean {
  if (obj === undefined) {
    return true;
  }
  if (obj === null) {
    return true;
  }
  return false;
}

export function isNullOrEmpty(obj: any): boolean {
  if (obj === undefined) {
    return true;
  }
  if (obj === null) {
    return true;
  }
  if (obj.length == 0) {
    return true;
  }
  return false;
}
export function isNullOrBlank(str: string): boolean {
  if (str == null) {
    return true;
  }
  if (str.trim().length == 0) {
    return true;
  }
  return false;
}

export function getQueryString(paraName: string) {
  const url = window.location.hash;
  const arrObj = url.split('?');
  if (arrObj.length > 1) {
    const arrPara = arrObj[1].split('&');
    let arr: any[];
    // tslint:disable-next-line: prefer-for-of
    for (let i = 0; i < arrPara.length; i++) {
      arr = arrPara[i].split('=');
      if (arr != null && arr[0] === paraName) {
        return arr[1];
      }
    }
    return '';
  } else {
    return '';
  }
}

export class FileTypeUtil {
  public static getFileExtension(valueField: any): any {
    let suffix = null;
    switch (valueField) {
      case FileConst.xls:
        suffix = '.xls';
        break;
      case FileConst.xlsx:
        suffix = '.xlsx';
        break;
      case FileConst.xlsm:
        suffix = '.xlsm';
        break;
      case FileConst.et:
        suffix = '.et';
        break;
      case FileConst.xml:
        suffix = '.xml';
        break;
      case FileConst.pdf:
        suffix = '.pdf';
        break;
      case FileConst.csv:
        suffix = '.csv';
        break;
      default:
        break;
    }
    return suffix;
  }

  public static getFileTypeName(fileType) {
    let fileTypeName = '';
    switch (fileType) {
      case 'XLS':
        fileTypeName = FileConst.xls;
        break;
      case 'XLSX':
        fileTypeName = FileConst.xlsx;
        break;
      case 'XLSM':
        fileTypeName = FileConst.xlsm;
        break;
      case 'ET':
        fileTypeName = FileConst.et;
        break;
      case 'XML':
        fileTypeName = FileConst.xml;
        break;
      case 'PDF':
        fileTypeName = FileConst.pdf;
        break;
      case 'CSV':
        fileTypeName = FileConst.csv;
        break;
      default:
        break;
    }
    return fileTypeName;
  }
}

export class DataIEUtils {
  static ChooseSu(repository: any, option: any): any {
    let su = '';
    // éç¨å¯¼å¥å¯¼åºä»åºå­å¨ï¼ä½¿ç¨ä¸é¢åæ¯
    if (repository !== null) {
      const befRestService = DataIEUtils.GetBefRestService(repository);
      su = DataIEUtils.getSu(befRestService.baseUri);
    }
    // æå·¥å¼åçä»åºä¸å­å¨ï¼éç¨ä¼ å¥çsu
    if (option['su'] !== undefined && option['su'] !== null) {
      su = option['su'];
    }
    return su;
  }

  static getSu(preurl: string): any {
    const str = preurl.substring(preurl.indexOf("//") + 2);
    let strs: string[] = str.split('/');
    const su = strs[3];
    return su;
  }

  static getApp(preurl: string): any {
    const str = preurl.substring(preurl.indexOf("//") + 2);
    let strs: string[] = str.split('/');
    const app = strs[2];
    return app;
  }

  public static getPort(preurl: string): any {
    // "http://localhost:5000/api/scm/sd/v1.0"
    const str = preurl.substring(preurl.indexOf("//") + 2);
    // åªååä¸¤ä½ï¼51xx,50xx,52xx
    const port = str.substr(str.indexOf(':') + 1, 2);
    return port;
  }

  public static GetBefRestService(repository: Repository<any>): BefRestService {
    const befRepository = <BefRepository<any>>repository;
    return befRepository.restService;
  }

  public static GetPreUrl(baseUrl: string): string {
    const lastIndex = baseUrl.lastIndexOf('/');
    const preurl = baseUrl.substr(0, lastIndex);
    return preurl;
  }

  public static JudgeIllegalChar(str: string): boolean {
    const illegalChar = ['?', 'ã', '\\', '/', '*', '\'', '"', 'â', 'â', '<', '>', '|', ',', ' '];
    illegalChar.forEach(item => {
      if (str.indexOf(item) !== -1) {
        return true;
      }
    });
    return false;
  }

  public static getTranslateValue(http: HttpClient): Observable<any> {
    let lang: string;
    if (localStorage === undefined || localStorage === null) {
      return of(DefaultLang['zh-CHS']);
    }
    lang = localStorage.getItem('languageCode');

    if (lang === undefined || lang === null || lang === 'zh-CHS') {
      return of(DefaultLang['zh-CHS']);
    }
    if (http === undefined || http === null) {
      return of(DefaultLang['zh-CHS']);
    }
    const subject = new Subject<any>();
    let url: string = `/platform/common/web/@gsp-dip/i18n/${lang}.json`;
    http.get(url).subscribe(data => {
      console.log(data);
      subject.next(data);
    });
    return subject;
  }
}
