  private ExportCommonEntry(ruleID: string, option: string, methodType: MethodType): Observable<any> {
    const subject = new Subject<any>();
    const optionJson = this.ParseExportJson(option);
    console.log(optionJson);
    if (isNullOrEmpty(ruleID) && isNullOrEmpty(optionJson.voId)) {
      this.notify.warning("æªéç½®è§åidï¼ä¸æ æ³è·åå°è¡¨åå¯¹åºçvoIdï¼è¯·æ£æ¥");
      const res = {
        type: 'close',
        data: null,
        other: null
      };
      subject.next(res);
      return subject;
    }
    if (methodType === MethodType.ExportCurrentChildData) {
      if (isNullOrEmpty(ruleID) && isNullOrEmpty(optionJson.exportChildNodeCode)) {
        //å¯¼åºå­è¡¨å½è§åidä¸ºç©ºæ¶ï¼æ ¹æ®voæ¾è§åï¼å¿é¡»è¦æ±æéç½®å­è¡¨èç¹
        this.notify.warning("è§åidä¸ºç©ºæ¶ï¼æ ¹æ®Voæ¥æ¾è§åï¼å¯¼åºå­è¡¨å¿é¡»éç½®å­è¡¨èç¹");
        const res = {
          type: 'close',
          data: null,
          other: null
        };
        subject.next(res);
        return subject;
      }
    }
    if (isNullOrEmpty(ruleID) || optionJson.enableRuntimeCustom === true) {
      this.StartLoading();
      const url = '/api/runtime/dip/v1.0/dataIeRuleManage/getRecordRuleInfo';
      const postBody = {
        body: {
          ruleId: ruleID,
          ruleType: 1,
          voId: optionJson.voId,
          needModelConvert: optionJson.needModelConvert === true,
          methodType: methodType,
          exportChildNodeCode: optionJson.exportChildNodeCode,
          enableRuntimeCustom: optionJson.enableRuntimeCustom === true
        }
      };

      this.http.post(url, postBody.body).subscribe((recordRule:any) => {
        this.CloseLoading();
        optionJson.voId = recordRule.voId;
        this.ExportByRuleImpl(ruleID, optionJson, methodType, recordRule).subscribe(s => {
          subject.next(s);
          subject.complete();
        });
      }, error => {
        this.CloseLoading();
        this.notify.error(error.error.Message);
        console.log(error.error.Message);
        const res = {
          type: 'close',
          data: null,
          other: error.error.Message
        };
        subject.next(res);
      });
    } else {
      this.ExportByRuleImpl(ruleID, optionJson, methodType).subscribe(s => {
        subject.next(s);
        subject.complete();
      });
    }
    return subject;
  }

  private ExportByRuleImpl(ruleID: string, optionJson: any, mType: MethodType, recordRule?: any) {
    const subject = new Subject<any>();
    if (optionJson.customExportTitle !== undefined && optionJson.customExportTitle !== null) {
      if (optionJson.customExportTitle.length > 20) {
        this.notify.warning('èªå®ä¹æ é¢åç§°çé¿åº¦å¤ªé¿');
        const res = {
          type: 'close',
          data: null,
          other: null
        };
        subject.next(res);
        return subject;
      }
      if (DataIEUtils.JudgeIllegalChar(optionJson.customExportTitle)) {
        this.notify.warning('èªå®ä¹æ é¢åç§°å­å¨ä¸åæ³å­ç¬¦');
        const res = {
          type: 'close',
          data: null,
          other: null
        };
        subject.next(res);
        return subject;
      }
    }

    if (optionJson.enableRuntimeCustom === true || optionJson.selectfiletype === true) {
      const compFactory = this.resolver.resolveComponentFactory(ExportCustomSelectHandcraftComponent);
      const cmpRef = compFactory.create(this.injector);
      cmpRef.instance.ruleId = ruleID;
      cmpRef.instance.exportOption = optionJson;
      cmpRef.instance.recordRule = recordRule;
      cmpRef.instance.subject = subject;
      cmpRef.instance.methodType = MethodType.CommonExport;
      let title = this.data_export;
      if (optionJson.customExportTitle !== undefined && optionJson.customExportTitle !== null) {
        title = optionJson.customExportTitle;
      }
      const exportModalConfig = {
        title: title,
        width: 530,
        height: 342,
        buttons: cmpRef.instance.modalFooter,
        showCloseButton: true,
        showMaxButton: false,
        showMinButton: false,
        showButtons: true,
        resizable: false
      };
      const dialog = this.modalService.show(cmpRef, exportModalConfig);
      dialog.dialog.instance.closed.subscribe(() => {
        const res = {
          type: 'close',
          data: null,
          other: null
        };
        subject.next(res);
      });
      cmpRef.instance.needDestroy.subscribe($event => {
        dialog.close();
      });
    } else {
      if (!isNullOrUndefined(recordRule) && !isNullOrEmpty(recordRule.ruleId)) {
        ruleID = recordRule.ruleId;
      }
      if (mType == MethodType.CommonExport) {
        this.dataExportService.ExportById4HandCraft(ruleID,  optionJson);
      } else if (mType == MethodType.ExportCurrentTableData){
        throw new Error('ä¸æ¯æè¯¥ç±»å:ExportCurrentTableData');
      }else{
        throw new Error('ä¸æ¯æè¯¥ç±»å:ExportCurrentChildData');
      }
      const unscriber = this.dataExportService.export.subscribe(data => {
        if (data === '') {
          const res = {
            type: 'success',
            data: null,
            other: null
          };
          subject.next(res);
          subject.complete();
        } else {
          const errorlevel = this.dataExportService.errorlevel;
          if (errorlevel === 'Info') {
            this.notify.info(data);
          } else if (errorlevel === 'Warning') {
            this.notify.warning(data);
          } else {
            this.notify.error(this.export_fail + data);
          }
          const res = {
            type: 'fail',
            data: null,
            other: data
          };
          subject.next(res);
          subject.complete();
        }
        unscriber.unsubscribe();
      }, error => {
        this.notify.error(error.error.Message);
        const res = {
          type: 'fail',
          data: null,
          other: error.error.Message
        };
        subject.next(res);
        subject.complete();
      });
    }
    return subject;
  }






