import { Injectable, Output, EventEmitter, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { DataIEUtils } from '../Utils/DataIEUtils';

@Injectable()
export class FileOperateService {
  private origin = window.location.origin;
  private fileUrl = this.origin + '/api/runtime/rtcommon/v1.0/fileoper';
  public downloadComplete: any;
  private file_download_fail: string;
  constructor(
    private http: HttpClient,
  ) {
    this.downloadComplete = new EventEmitter();
    DataIEUtils.getTranslateValue(http).subscribe(res => {
      this.file_download_fail = res['file_download_fail'];
    });
  }
  // ä¸è½½æä»¶
  DownloadFile(relativePath: string, fileName: string, preurl: string, option: any, needDelete: boolean = true) {

    let tempUrl = '';
    if (preurl === '') {
      tempUrl = this.fileUrl;
    } else {
      tempUrl = `${preurl}/fileoper`;
    }

    const app = DataIEUtils.getApp(tempUrl);
    let su = DataIEUtils.getSu(tempUrl);
    if (option !== null && option['su'] !== null && option['su'] !== undefined) {
      su = option['su'];
    }
    const fileParam = {
      relativePath: relativePath,
      app: app,
      su: su
    };
    const fixedUrl = this.origin + '/api/runtime/dip/v1.0/rpcfileoper';

    this.http.post(
      `${fixedUrl}/DownloadFile`,
      fileParam,
      {
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*'
        },
        responseType: 'blob',
        observe: 'response',
        params: { su: su }
      }
    ).subscribe(filedata => {
      if (!filedata || filedata.body.size == 0) {
        this.downloadComplete.emit(this.file_download_fail);
      } else {
        //const blob = new Blob([filedata.body], {type: 'application/vnd.ms-excel'});
        const blob = new Blob([filedata.body]);
        if (navigator.msSaveOrOpenBlob) {
          navigator.msSaveOrOpenBlob(blob, fileName);
        } else {
          const link = document.createElement('a');
          link.setAttribute('href', window.URL.createObjectURL(blob));
          // è®¾ç½®ä¸è½½çæä»¶å
          link.setAttribute('download', fileName);
          link.style.visibility = 'hidden';
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
        }
        this.downloadComplete.emit('');
        if (needDelete) {
          this.DeleteFile(relativePath, preurl, su);
        }
      }
    }, error => {
      this.downloadComplete.error(error);
    });
    // æ·»å errorå¼å¸¸å¤ç
  }
  // æåæä»¶ä¸è½½æä»¶
  DownloadPackageFile(relativePaths: any, itemsFileName: any, packageName: string, preurl: string, packtype: string, needDelete: boolean = true) {

    let tempUrl = '';
    if (preurl === '') {
      tempUrl = this.fileUrl;
    } else {
      tempUrl = `${preurl}/fileoper`;
    }

    const app = DataIEUtils.getApp(tempUrl);
    const su = DataIEUtils.getSu(tempUrl);
    const fileParam = {
      relativePaths: relativePaths,
      itemsFileName: itemsFileName,
      packType: packtype,
      packageName: packageName,
      su: su
    };
    const fixedUrl = this.origin + '/api/runtime/dip/v1.0/rpcfileoper';

    this.http.post(
      `${fixedUrl}/DownloadPackageFile`,
      fileParam,
      {
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*'
        },
        responseType: 'blob',
        observe: 'response',
        params: { su: su }
      }
    ).subscribe(filedata => {
      if (!filedata || filedata.body.size == 0) {
        this.downloadComplete.emit(this.file_download_fail);
      } else {
        const blob = new Blob([filedata.body]);
        // è®¾ç½®ä¸è½½çæä»¶å
        const downloadFileName = packageName + '.' + packtype;
        if (navigator.msSaveOrOpenBlob) {
          navigator.msSaveOrOpenBlob(blob, downloadFileName);
        } else {
          const link = document.createElement('a');
          link.setAttribute('href', window.URL.createObjectURL(blob));
          link.setAttribute('download', downloadFileName);
          link.style.visibility = 'hidden';
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
        }
        this.downloadComplete.emit('');
        if (needDelete) {
          this.DeleteFile(downloadFileName, preurl);
        }
      }
    }, error => {
      this.downloadComplete.error(error);
    });
    // æ·»å errorå¼å¸¸å¤ç
  }
  /**
   * æå·¥è¡¨åä¸è½½
   * @param relativePath 
   * @param fileName 
   * @param su 
   * @param needDelete 
   */
  DownloadFile4HandCraft(relativePath: string, fileName: string, su: string, needDelete: boolean = true) {
    const fileParam = {
      relativePath: relativePath,
      app: '',
      su: su
    };
    const fixedUrl = this.origin + '/api/runtime/dip/v1.0/rpcfileoper';

    this.http.post(
      `${fixedUrl}/DownloadFile`,
      fileParam,
      {
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*'
        },
        responseType: 'blob',
        observe: 'response',
        params: { su: su }
      }
    ).subscribe(filedata => {
      if (!filedata || filedata.body.size == 0) {
        this.downloadComplete.emit(this.file_download_fail);
      } else {
        const blob = new Blob([filedata.body]);
        if (navigator.msSaveOrOpenBlob) {
          navigator.msSaveOrOpenBlob(blob, fileName);
        } else {
          const link = document.createElement('a');
          link.setAttribute('href', window.URL.createObjectURL(blob));
          // è®¾ç½®ä¸è½½çæä»¶å
          link.setAttribute('download', fileName);
          link.style.visibility = 'hidden';
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
        }
        this.downloadComplete.emit('');
        if (needDelete) {
          this.DeleteFile4HandCraft(relativePath, su);
        }
      }
    }, error => {
      this.downloadComplete.error(error);
    });
    // æ·»å errorå¼å¸¸å¤ç
  }
  // å é¤æä»¶
  DeleteFile(relativePath: string, preurl: string, customSu: string = '') {
    let tempUrl = '';
    if (preurl === '') {
      tempUrl = this.fileUrl;
    } else {
      tempUrl = `${preurl}/fileoper`;
    }
    let su = '';
    if (customSu !== '') {
      su = customSu;
    } else {
      su = DataIEUtils.getSu(tempUrl);
    }
    const delParam = {
      relativePath: relativePath,
      su: su
    };
    const fixedUrl = this.origin + '/api/runtime/dip/v1.0/rpcfileoper';
    this.http.post(
      `${fixedUrl}/DeleteFile`,
      delParam,
      {
        params: { su: su }
      }
    ).subscribe();
  }
  DeleteFile4HandCraft(relativePath: string, su: string) {
    const delParam = {
      relativePath: relativePath,
      su: su
    };
    const fixedUrl = this.origin + '/api/runtime/dip/v1.0/rpcfileoper';
    this.http.post(
      `${fixedUrl}/DeleteFile`,
      delParam,
      {
        params: { su: su }
      }
    ).subscribe();
  }
}
