import {Component, ComponentFactoryResolver, Injector, OnInit} from '@angular/core';
import {Observable, Subject} from "rxjs";
import {BsModalService} from "@farris/ui-modal";
import {RuleIdHelpComponent} from "../rule-id-help/rule-id-help.component";

@Component({
  selector: 'dataie-rule-help',
  templateUrl: './dataie-rule-help.component.html',
  styleUrls: ['./dataie-rule-help.component.css']
})
export class DataieRuleHelpComponent implements OnInit {

  constructor(
    private injector: Injector,
    private modalService: BsModalService,
    private resolver: ComponentFactoryResolver
  ) { }

  ngOnInit() {
  }

  /**
   * æå¼å¯¼å¥å¯¼åºè§åidéæ©å¸®å©
   * @param ruleType è§åç±»åï¼å¯¼å¥ä¸º0ï¼å¯¼åºä¸º1
   * @param voId
   * @param beId
   */
  public showRuleIdHelp(ruleType:number,voId:string,beId:string): Observable<any>{
    const subject = new Subject<any>();
    const compFactory = this.resolver.resolveComponentFactory(RuleIdHelpComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.beId = beId;
    compRef.instance.ruleType = ruleType;
    compRef.instance.voId = voId;
    const modalConfig = {
      title: 'è§åéæ©',
      width: 920,
      height: 500,
      buttons: compRef.instance.modalFooter,
      showHeader: true,
      showMaxButton: false,
      showButtons: true
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.closeModal.subscribe(ruleId => {
      console.log(ruleId);
      dialog.close();
      subject.next(ruleId);
    });
    return subject;
  }

}
