export class FileLikeObject {
    public lastModifiedDate: any;
    public size: any;
    public type: string;
    public name: string;
    public rawFile: string;

    public constructor(fileOrInput: any) {
    this.rawFile = fileOrInput;
    const isInput = this.isElement(fileOrInput);
    const fakePathOrObject = isInput ? fileOrInput.value : fileOrInput;
    const postfix = typeof fakePathOrObject === 'string' ? 'FakePath' : 'Object';
    const method = 'createFrom' + postfix;
    (this as any)[ method ](fakePathOrObject);
    }
    isElement(node: any): boolean {
        return !!(node && (node.nodeName || node.prop && node.attr && node.find));
    }
    createFromFakePath(path: string): void {
        this.lastModifiedDate = void 0;
        this.size = void 0;
        this.type = 'like/' + path.slice(path.lastIndexOf('.') + 1).toLowerCase();
        this.name = path.slice(path.lastIndexOf('/') + path.lastIndexOf('\\') + 2);
      }
    createFromObject(object: { size: number, type: string, name: string }): void {
        this.size = object.size;
        this.type = object.type;
        this.name = object.name;
      }
}



